.country-card {
    min-height: 340px;
    max-width: 300px;
    min-width: 220px;
    margin: 16px;
    width: 100%;
    color: #191919;
}
.country-flag {
    height: 220px;
    width: 100%;
    object-fit: contain;
}
.country-name {
    font-weight: 900!important;
    font-size: 28px!important;
    line-height: 1.1!important;
    margin-bottom: 8px!important;
}
.sub-title {
    font-weight: 700!important;
}